import React, { Component } from "react";
import Footer from "../Footer";

export default class Help extends Component {
  componentDidMount() {

  }
  render() {
    return (
      <div>
        <meta name="robots" content="noindex"></meta>
        <h1>Page d'aide Nytuo (Site, Jeux, Launcher) :</h1>
        <p>
          Cette page permet de vous aider en cas de problme avec le Nytuo
          Launcher, le site ou mme les jeux.
          <br />
          Cette page est suceptible  des changements.
        </p>
        <h1>Nytuo Launcher :</h1>

        <div className="accordion accordion-flush" id="accordionFlushExample">
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingOne">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseOne" aria-expanded="false" aria-controls="flush-collapseOne">
                Avertissements
              </button>
            </h2>
            <div id="flush-collapseOne" className="accordion-collapse collapse" aria-labelledby="flush-headingOne"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body">Le Nytuo Launcher est souvent dtecter par les antivirus comme
                un virus car il modifie les dossiers et les fichiers du
                Launcher.Vous pouvez vrifier les fichiers avec Virus Total
                qui permet lanalyse sur plusieurs base de donnes.Si vous
                souhaiter utilis le launcher mais que votre antivirus le
                dtecte, il faut restaur les fichiers en quarantaine et
                mettre le dossier gnrale en exclusion danalyse et de
                lancement. Paramtres diffrents entre les antivirus.
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingTwo">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseTwo" aria-expanded="false" aria-controls="flush-collapseTwo">
                Tlchargement, installation et lancement
              </button>
            </h2>
            <div id="flush-collapseTwo" className="accordion-collapse collapse" aria-labelledby="flush-headingTwo"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><h1>Windows : </h1>
                <p>
                  I/ En cliquant sur le boutton 'Tlcharger le Nytuo Launcher'
                  sur la page du site concernant le Nytuo-Launcher, Tlcharger
                  sur Github le fichier vous convenant.
                </p>
                <p>
                  Note : Pour une installation calssique sur votre ordinateur,
                  choisissez, le "Nytuo-Launcher-Setup-[dernire version].exe",
                  pour une version portable choisissez,le
                  "Nytuo-Launcher-[dernire version]-win.zip" et allez voir la
                  section "Portable Edition"
                </p>
                <p>(Uniquement pour ce qui ont choisi le ".exe" )</p>
                <p>II/Lancer l'installeur, toutes est automatique!</p>
                <p>
                  III/Windows : Aprs l'installation, le Nytuo launcher s'ouvre
                  automatiquement. Un raccourcie  galement tait cr sur le
                  bureau.
                </p>
                <p>
                  Lors du premier lancement du launcher vous serez inviter 
                  selectionner un dossier o stocker les jeux (ce dossier doit
                  tre ddier et exclusif. Il peut provenir d'une autre
                  installation et dj contenir les jeux(en respectant la
                  hirarchisation)). Vous devrez galement choisir une langue
                  (Franais, Anglais ou Espagnol)
                </p>
                <h1>Linux : </h1>
                <p>
                  I/ En cliquant sur le boutton 'Tlcharger le Nytuo Launcher'
                  sur la page du site concernant le Nytuo-Launcher, Tlcharger
                  sur Github le fichier vous convenant.
                </p>
                <p>
                  Note : Pour une utilisation classique sur votre ordinateur,
                  choissisez,"Nytuo-Launcher-Setup-[dernire
                  version].AppImage".Pour une version portable,
                  choisissez,"nytuolauncher-[dernire version].zip" et allez
                  voir la section "Portable Edition"{" "}
                </p>
                <p>
                  II/Clique droit sur le ".appimage" puis "proprits".Cocher
                  dans "permissions" la case "Executer comme une
                  application".Double cliques sur le fichier ".appimage" pour
                  l'executer.
                </p>
                <p>
                  Lors du premier lancement du launcher vous serez inviter 
                  selectionner un dossier o stocker les jeux (ce dossier doit
                  tre ddier et exclusif. Il peut provenir d'une autre
                  installation et dj contenir les jeux(en respectant la
                  hirarchisation)). Vous devrez galement choisir une langue
                  (Franais, Anglais ou Espagnol)
                </p>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingThree">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseThree" aria-expanded="false" aria-controls="flush-collapseThree">
                Dossier des jeux
              </button>
            </h2>
            <div id="flush-collapseThree" className="accordion-collapse collapse" aria-labelledby="flush-headingThree"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body">Pour que vous soyez plus libre, le dossier des jeux est
                dynamique, c'est  vous de le choisir lors de la premire
                utilisation ou dans les paramtres pour le changer.Il peut
                provenir d'une autre installation et dj contenir les jeux(en
                respectant la hrarchisation (Games\[ID]\[Game files]))).
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingFour">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseFour" aria-expanded="false" aria-controls="flush-collapseFour">
                Backup des jeux
              </button>
            </h2>
            <div id="flush-collapseFour" className="accordion-collapse collapse" aria-labelledby="flush-headingFour"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                Dans le cadre d'une sauvegarde des jeux manuellement,
                <br />
                I/Dans les paramtres se trouve l'emplacement du dossier
                "Games" contenant les jeux.Suivez cette emplacement dans
                l'explorateur de fichier.
                <br />
                II/ Copier et coller le dossier "Games" ailleurs sur votre
                machine. <br />
                Note: Le dossier "Games" se trouve en cas d'erreur dans vos
                Documents sur Linux ou au dossier prcdent le dossier du
                Nytuo-Launcher sur Windows.Ce dossier tant attribu
                dynamiquement (c'est  vous de dterminer son emplacement lors
                de la premire utilisation), il est difficile de pouvoir vous
                aider plus...
              </p>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingFive">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseFive" aria-expanded="false" aria-controls="flush-collapseFive">
                Mettre  jour le Nytuo Launcher
              </button>
            </h2>
            <div id="flush-collapseFive" className="accordion-collapse collapse" aria-labelledby="flush-headingFive"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                Sur Windows: Si une mise  jour est disponible, le Nytuo
                Launcher la tlcharge au dmarrage, se ferme et lance un
                Batch (fichier en .bat lanant un invite de commandes (CMD))
                de manire  lancer le programme de mise  jour de manire
                diffr et que le launcher puisse se fermer correctement.
                <br />
                Sur Linux: La mise  jour, si disponible se tlcharge et sera
                accessible directement sur le bureau Linux. Vous pourrez alors
                la dplac et mme supprimer l'autre version manuellement
                <br />
                Note : Vous pouvez forcer la mise  jour du launcher depuis
                les paramtres
                <br />
                Note II : en mode portable, les mises  jour sont dsactiver,
                le guide pour mettre  jour le launcher en mode portable se
                trouve dans la section "portable".
              </p>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingSix">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseSix" aria-expanded="false" aria-controls="flush-collapseSix">
                Dinstallation
              </button>
            </h2>
            <div id="flush-collapseSix" className="accordion-collapse collapse" aria-labelledby="flush-headingSix"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                Sur Windows: <br />
                <br />
                La dinstallation se fait en trois temps:
                <br />
                (Seulement si vous souhaiter galement supprimer les jeux)
                <br />
                Aller dans le Nytuo Launcher, dans les paramtres, regard la
                ligne concernant l'emplacement du dossier des jeux.Fermer le
                launcher et aller  cette emplacement et supprimer-le.
                <br />
                I/Dinstaller le logiciel comme n'importe qu'elle autres, dans
                les paramtres.
                <br />
                II/Dans le dossier "Local" de "Appdata", trouver le dossier
                "nytuolauncher" et "nytuolauncher_data" et supprimer les.
                <br />
                III/Vous pouvez effectuer un redmarrage au besoin
                <br />
                Vous venez de supprimer le Launcher de votre machine
                <br />
                <br />
                Sur Linux :<br />
                (Seulement si vous souhaiter galement supprimer les jeux)
                <br />
                Aller dans le Nytuo Launcher, dans les paramtres, regard la
                ligne concernant l'emplacement du dossier des jeux.Fermer le
                launcher et aller  cette emplacement et supprimer-le.
                <br />
                I/Supprimer le .appimage du Nytuo Launcher
                <br />
                II/Supprimer dans le dossier "Documents", le dossier
                "nytuolauncher_data".
                <br />
                IV/Redmarrer l'ordinateur pour enlever toutes traces local.
                <br />
                Vous venez de supprimer le Launcher de votre machine
                <br />
                <br />
                Pour les versions Portable :<br />
                Mettez le dossier contenant les jeux, le launcher et le
                nytuolauncher_data  la corbeille et videz-l.
              </p>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingSeven">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseSeven" aria-expanded="false" aria-controls="flush-collapseSeven">
                Portable Edition
              </button>
            </h2>
            <div id="flush-collapseSeven" className="accordion-collapse collapse" aria-labelledby="flush-headingSeven"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                Le Nytuo Launcher existe en version portable. Elle ne
                ncessite donc pas d'installation.Cependant, les mises  jour
                automatiques sont dsactivs et le dossier des jeux ne peut
                tre choisi.
              </p>
                <p>
                  I/Pour obtenir la version portable du launcher, tlcharger
                  depuis GitHub le zip : "Nytuo-Launcher-[dernire
                  version]-win.zip" pour windows et le zip :
                  "nytuolauncher-[dernire version].zip" pour Linux.O [dernire
                  version] correspond  la dernire version du launcher.
                </p>
                <p>
                  II/Crer un dossier "Nytuo" pour accueillir le tout (le nom de
                  ce dossier n'a pas d'importance mais c'est logique).Dans ce
                  dossier crer un dossier "Nytuo-Launcher"(encore une fois, le
                  nom n'a pas d'importance) puis extrayer le contenu de
                  l'archive dans ce dossier.
                </p>
                <p>
                  III/Aller dans le dossier "Nytuo-Launcher" puis aller dans :
                  "Resources/app/", ouvrez le fichier "portable.txt" et modifier
                  le "false" en "true".Cela permet d'activer les fonctionnalits
                  portable. Sinon le launcher fonctionnera mais les mises  jour
                  causeront problmes et le dossier des jeux ne sera pas
                  portable.
                </p>
                <p>
                  IV/Par la suite vous avez juste, dans le dossier
                  "Nytuo-Launcher" a lanc le "Nytuo-Launcher.exe"
                </p>
                <p>
                  Pour les mises  jour, supprimer le contenu du dossier
                  "Nytuo-Launcher" (mais pas le dossier) et refaite les tapes
                  depuis la I sans cr de nouveau dossiers et en utilisant ceux
                  existants.{" "}
                </p>
                <p>
                  Pour dinstaller le launcher, vous devez mettre le dossier
                  "Nytuo"  la corbeille et la vider, si vous voulez garder les
                  jeux ne supprimer que les dossier "Nytuo-Launcher" et
                  "nytuolauncher_data".
                </p>
                <p>
                  Note : Si vous devez dplacer le logiciel, vous devez dplacer
                  le dossier "Nytuo" en entier.
                  <br />
                  Note II : le launcher va cr deux nouveaux dossiers dans le
                  dossier "Nytuo".Un pour les jeux, l'autre pour les paramtres
                  externes.
                </p>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingEight">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseEight" aria-expanded="false" aria-controls="flush-collapseEight">
                En cas d'erreurs
              </button>
            </h2>
            <div id="flush-collapseEight" className="accordion-collapse collapse" aria-labelledby="flush-headingEight"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                Visiter la section "FeedBack" du site ou rendez-vous sur
                GitHub et cr une nouvelle "issue"
              </p>
                <p>Je corrige les bugs quand je les connais au plus vite...</p>
              </div>
            </div>
          </div>
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingNine">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseNine" aria-expanded="false" aria-controls="flush-collapseNine">
                Notes
              </button>
            </h2>
            <div id="flush-collapseNine" className="accordion-collapse collapse" aria-labelledby="flush-headingNine"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                I/ Sous Linux, pendant l'execution d'un jeu le launcher ne
                fonctionne pas ! Vous devez fermer le jeu pour retrouver
                l'accs au launcher.
              </p>
                <p>
                  II/ Sous Linux, Pour lancer le launcher dans le cas o,
                  celui-ci ne ce lance pas, veuillez  cocher la case dans
                  l'onglet "permissions" des "proprits" du fichier et
                  ressayer.Si l'erreur persiste, envoyer un feedback.
                </p>
              </div>
            </div>
          </div>
        </div>

        <h1>Gnrale :</h1>
        <div className="accordion accordion-flush" id="accordionFlushExample">
          <div className="accordion-item">
            <h2 className="accordion-header" id="flush-headingTen">
              <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                      data-bs-target="#flush-collapseTen" aria-expanded="false" aria-controls="flush-collapseTen">
                Comptes
              </button>
            </h2>
            <div id="flush-collapseTen" className="accordion-collapse collapse" aria-labelledby="flush-headingTen"
                 data-bs-parent="#accordionFlushExample">
              <div className="accordion-body"><p>
                <strong>Intro et avertissement:</strong>
                <br />
                Depuis la dernire mise  jour en date du 09/04/2021 pour le
                launcher et de quelques jours plus tot pour le site internet,
                le systeme de compte est maintenant grer par Google Firebase,
                permettant la connexion avec des services comme Google,
                Github, Twitter, Microsoft ou encore Facebook et bien sre
                toujours avec la combinaison mail/ mot de passe.
                <br />
                La connexion est grer par FireBase, tout est scuriser. Pour
                plus d'informations, aller directement sur le site de Google
                Firebase ou poser votre question dans la rubrique FeedBack.
                <br />
                Vous pouvez bien sre aller consulter le project du site sur{" "}
                <a href="https://github.com/Nytuo/Nytuo-Website_Project">
                  GitHub
                </a>
                .<br />
                <strong>Connexion au compte dans le Nytuo-Launcher:</strong>
                <br />
                1.Quand la page de connexion apparait, connecter vous ! 2.Un
                message apparaitera aprs vous tre connect. Vous allez
                devoir fermer tout fentre du Launcher et relancer ce dernier.
                3.La connexion s'effectue tout seul automatiquement et le
                launcher continu vers la page principale. Si jamais vous
                abandonnez la connexion (vous quittez le launcher avant/sans
                de vous tre connect), le prochain lancement vous aurez un
                message qui vous demandera de fermer les fentre et de
                redmarrer le launcher. En cas d'erreur envoyer moi un
                feedback.
                <strong>Dconnexion du compte sur le Launcher</strong>
                <br />
                Connecter dans le launcher depuis n'importe qu'elle page
                cliquer dans la bar de navigation  gauche sur 'se
                dconnecter'. Sur Windows, un CMD se lance (tout est
                automatique) et au bout de 5 secondes, apres fermeture du
                launcher, il vous dconnecte en supprimant toutes traces de
                votre compte dans le Launcher. Sur Linux, cela est un peu
                diffrent, vous devrez ouvrir un Terminal et mettre les
                commandes dans l'ordre :{" "}
                <strong>cd ~/snap/nytuo-launcher/x1/.config</strong> et{" "}
                <strong>rm -rf Nytuo-Launcher</strong>. Vous pouvez alors
                redmarrer le launcher et vous Connectez.
              </p>
              </div>
            </div>
          </div>
        </div>

        <h1>Questionnements :</h1>
        <p>Visiter la section "FeedBack" du site</p>
        <Footer />
      </div>
    );
  }
}
